import React from 'react'
import { Box, Typography } from '@mui/material'

export interface DescriptionListProps {
    items: string[]
}

const DescriptionList = (props: DescriptionListProps) => {
    const { items } = props
    return (
        <Box
            component='ul'
            color='text.secondary'
            sx={{
                position:       'relative',
                listStyle:      'none',
                margin:         0,
                marginLeft:     '0',
                paddingLeft:    '1.2em',
                '& li::before': {
                    content:  '"-"',
                    position: 'absolute',
                    left:     0
                }
            }}
        >
            {items.map((prop, idx) => <Box
                key={idx}
                component='li'
            >
                <Typography color='text.secondary'>{prop}</Typography>
            </Box>)}
            
        </Box>
    )
}

export default DescriptionList
