// import React, { Suspense } from 'react';
import * as React from 'react'
import { Box, Typography } from '@mui/material'
import { Section } from 'components'

const Education = () => {
    return (
        <Section title={'education'}>
            <Box p='0 10px'>
                <Box sx={{
                    display: 'flex'
                }}
                >
                    <Box>
                        <Typography
                            variant='h6'
                            sx={{
                                fontSize: '1rem'
                            }}
                        >
                            IT Academy STEP
                        </Typography>
                        <Box sx={{
                            paddingTop: '10px'
                        }}
                        >
                            <Typography
                                color='text.secondary'
                                sx={{
                                    borderLeft: '1px solid #f26b38',
                                    display:    'inline-block',
                                    position:   'relative',
                                    padding:    '24px 10px 20px 10px',
                                    '&::after': {
                                        content:         '"01/09/2017 - 01/09/2018"',
                                        position:        'absolute',
                                        display:         'flex',
                                        alignItems:      'center',
                                        height:          '24px',
                                        top:             -12,
                                        left:            -1,
                                        backgroundColor: '#f26b38',
                                        padding:         '2px 10px',
                                        color:           'white'
                                    }
                                }}
                            >
                                Graduated front-end courses.
                            </Typography>
                        </Box>
                    </Box>
                </Box>
                <Box sx={{
                    display:    'flex',
                    paddingTop: '20px'
                }}
                >
                    <Box>
                        <Typography
                            variant='h6'
                            sx={{
                                fontSize: '1rem'
                            }}
                        >
                            WSZiA w Opolu
                        </Typography>
                        <Box sx={{
                            paddingTop: '10px'
                        }}
                        >
                            <Typography
                                color='text.secondary'
                                sx={{
                                    borderLeft: '1px solid #f26b38',
                                    display:    'inline-block',
                                    position:   'relative',
                                    padding:    '24px 10px 20px 10px',
                                    '&::after': {
                                        content:         '"01/10/2013 - 01/08/2017"',
                                        position:        'absolute',
                                        display:         'flex',
                                        alignItems:      'center',
                                        height:          '24px',
                                        top:             -12,
                                        left:            -1,
                                        backgroundColor: '#f26b38',
                                        padding:         '2px 10px',
                                        color:           'white'
                                    }
                                }}
                            >
                                Graduated from the university with a degree in Enterprise Management and Logistics.
                            </Typography>
                        </Box>
                    </Box>
                </Box>
            </Box>
        </Section>
    )
}

export default Education
