// import React, { Suspense } from 'react';
import * as React from 'react'
import { Box, Typography } from '@mui/material'
import { Section, DescriptionList } from 'components'

const WorkExpirience = () => {
    return (
        <Section title={'work expirience'}>
            <Box p='0 10px'>


                <Box sx={{
                    display: 'flex'
                }}
                >
                    <Box>
                        <Typography
                            variant='h6'
                            sx={{
                                fontSize: '1rem'
                            }}
                        >
                        RadiantRFID | Full Stack Developer
                        </Typography>
                        <Box sx={{
                            paddingTop: '10px'
                        }}
                        >
                            <Box
                                
                                sx={{
                                    borderLeft: '1px solid #f26b38',
                                    display:    'inline-block',
                                    position:   'relative',
                                    padding:    '24px 10px 20px 10px',
                                    '&::after': {
                                        content:         '"01/10/2018 - present"',
                                        position:        'absolute',
                                        display:         'flex',
                                        alignItems:      'center',
                                        height:          '24px',
                                        top:             -12,
                                        left:            -1,
                                        backgroundColor: '#f26b38',
                                        padding:         '2px 10px',
                                        color:           'white'
                                    }
                                }}
                            >
                                <DescriptionList items={[
                                    'Used javascript · AngularJS · ReactJS · C# · ASP.NET · SQL to design and develop IoT Platform and web-based solutions, both front end and back end.',
                                    'Trouble-shot and fixed threads and bugs.',
                                    'Communicated with clients to provide knowledge and support, ensuring that their requirements were fully met.'
                                ]}
                                />
                            </Box>
                        </Box>
                    </Box>
                </Box>
            </Box>
        </Section>
    )
}

export default WorkExpirience
