import React from 'react'
import { Scrollbars as CustomScrollbars, ScrollbarProps } from 'react-custom-scrollbars'

const Scrollbar: React.FC<ScrollbarProps & {
    position?: 'left' | 'right'
}> = (props) => {
    const { children, position = 'right', ...restProps } = props

    return (
        <CustomScrollbars
            renderTrackVertical={(e) => {
                return (<div
                    {...e}
                    className='scrollbars'
                    style={{
                        ...e.style,
                        height:        '100%',
                        right:         position == 'left' ? 'calc(100% - 4px)' : 0,
                        width:         '12px',
                        paddingTop:    '3px',
                        paddingBottom: '3px',
                        paddingLeft:   '1px'
                    }}
                />)

            }}
            renderThumbVertical={(e) =>
                <div
                    {...e}
                    className='asdasd'
                    style={{
                        ...e.style,
                        zIndex:          1299,
                        backgroundColor: '#bbb',
                        width:           '8px',
                        borderRadius:    '10px',
                        left:            1
                    }}
                />
            }
            universal={true}
            autoHide={true}
            {...restProps}
        >
            {children}
        </CustomScrollbars>
    )
}

export default Scrollbar
