import React from 'react'
import { styled, Box, BoxProps, Typography } from '@mui/material'
import GlitchClip from 'components/Glitch/GlitchClip'

const StyledTitle = styled(Typography)(({ theme }) => ({
    textTransform: 'uppercase',
    display:       'inline-block',
    fontSize:      '1.1rem',
    boxShadow:     `inset 0 -6px 0px ${theme.colors.primary}`
}))

const SectionTitle = styled((props: BoxProps) => {
    const { children, ...restProps } = props

    const randomIntFromInterval = (min: number, max: number): number => { // min and max included
        return Math.floor(Math.random() * (max - min + 1) + min)
    }

    return (
        <Box {...restProps}>
            <GlitchClip duration={randomIntFromInterval(1,7)}>
                <StyledTitle variant='h5'>
                    {children}
                </StyledTitle>
            </GlitchClip>
        </Box>
    )
})(() => ({
    paddingBottom: '20px'
}))

export default SectionTitle
